import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import AddUser from './add-user'
import EditUser from './edit-user'
import UserList from './users-list'

const Employees = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/add-user`} render={ () => (<AddUser systemData={systemData}/>) } />
			<Route path={`${match}/edit-user/:id`} component={EditUser} />			
			<Route path={`${match}/list`} render={ () => (<UserList systemData={systemData}/>) } />
		</Switch>
	)
}

export default Employees

